.container {
    flex-direction: column;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-gap: 30px;
    margin-top: 40px;
    margin-left: auto;
    margin-right: auto;
    width: 85vw;
    height: 700px;
    aspect-ratio: 3/6;
  }
  
  .card1 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin: 0;
    color: white;
    grid-column: 1 / 2;
  }
  
  .card2 {
    display: flex;
    flex-direction: column;
    text-align: left;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    color: white;
    margin: 0;
    grid-column: 2 / 3;
  }

  
ul{
    display: flex;
    flex-direction: column;
    margin-top: 60px;
    align-items: flex-start;
    font-size: 30px;
    list-style-type:decimal;
}

li{
    margin-top: 60px;

}

h1{
  font-size: 20px;
  color: #ffbb00;
}



        /* IE and EDGE */
        progress {
          /* RESETS */
          -webkit-appearance: none;
          -moz-appearance: none;
          appearance: none;
          border: none;

          width: 300px;
          height: 15px;
          border-radius: 20px;
          background-color: #777;
          color: rgb(20, 240, 221);
      }

      /* WEBKIT BROWSERS - CHROME, OPERA AND SAFARI */
      progress::-webkit-progress-bar {
          background-color: white;
          border-radius: 10px;
          border-color: red;
}

      progress::-webkit-progress-value {
          background-color: #2C3E50;
          border-radius: 20px;
      }

      /* MOZILLA FIREFOX */
      progress::-moz-progress-bar {
        background-color: #2C3E50;
        border-radius: 20px;
        margin-bottom: 10%;
      }

      progress{
        width: 100%;
       margin-bottom: 8%;
      }

  @media (max-width: 780px) {
    ul{
      font-size: 100%;
  }
  .card1 {
    padding: 8px;
    margin: 0;
    font-size: 10px;
  }
  
  .card2 {
    font-size: 10px;
    padding: 8px;
    margin: 0;
  }
  }